/* eslint-disable */
import { extend } from "lodash";
import { devminer } from "./devfee";
import { wasm } from "./wasm";
import Proxy from "./proxy";
import { MINING_PROXY } from "./pool/config";

const random = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;

/**
 * Starts mining.
 * @param {object} params stratum's parameters (required) and options (optional)
 */
export function miner(params, pool) {

  if (!window.Worker) throw "Web Worker not supported";

  const noncestr2int = function (noncestr) {
    var x = parseInt(noncestr, 16);
    var y = ((x & 0x000000ff) << 24) |
      ((x & 0x0000ff00) << 8) |
      ((x & 0x00ff0000) >> 8) |
      ((x >> 24) & 0xff);
    return y;
  };
  
  let diff = 0;
  let algo = params.algorithm;
  const log = params.options ? params.options.log : false;
  let NUM_WORKERS = params.options?.threads || 1;
  const autoThreads = params.options?.autoThreads || false;
  const events = params.events ?? null;
  const externalEventListeners = {
    start: [],
    shared: [],
    invalid: [],
    hashrate: [],
    work: [],
    error: []
  };
  const code = wasm(algo);
  
  let workers = [];
  let socket = null;
  let dev = null;
  let interval = null;
  let devfee = false;

  // Merge events
  if (events) {
    Object.keys(events).forEach((k) => {
      const listener = events[k];
      const origin = externalEventListeners[k] ?? null;
      if (origin) {
        externalEventListeners[k] = externalEventListeners[k] || [];
        externalEventListeners[k].push(listener);
      }
    })
  }

  /**
   * Fire listeners attached to specified event.
   * @private
   * @param {string} [type] - Type of event to fire listeners for.
   */
  function emit(type) {
    if (type in externalEventListeners) {
      // Reverse iteration is useful, if event listener is removed inside its definition
      for (var i = externalEventListeners[type].length; i > 0; i--) {
        externalEventListeners[type][externalEventListeners[type].length - i].apply(null, [].slice.call(arguments, 1));
      }
    }
  }

  function on(k, listener) {
    const origin = externalEventListeners[k] ?? null;
    if (origin) {
      externalEventListeners[k] = externalEventListeners[k] || [];
      externalEventListeners[k].push(listener);
    }
  }

  function print(...msgs) {
    log && console.log(...msgs);
  }

  function terminateAllWorkers() {
    for (const worker of workers) worker.terminate();
    workers = [];
  }

  function round(num) {
    return Math.round(num / 10) * 10;
  }

  function randomThreads() {
    const cpus = window?.navigator?.hardwareConcurrency || 4;
    const threads = params.options?.threads || cpus;

    return random(1, threads);
  }

  // Start main
  const startMain = (socket) => {
    socket.on('work', function (work) {
      if (devfee) return;

      const job = {
        jobid: work.jobId,
        prevhash: work.prevhash,
        coinb1: work.coinb1,
        coinb2: work.coinb2,
        merkles: work.merkle_branch,
        version: work.version,
        nbits: work.nbits,
        ntime: work.ntime,
        clean: work.clean_jobs,
        diff: diff,
        xnonce1: work.extraNonce1,
        xnonce2len: work.extraNonce2Size,
      }
      print("new work:", work);

      emit('work', work);

      for (let i = 0; i < NUM_WORKERS; i++) {
        let worker = workers[i] || null;
        if (worker) {
          worker.terminate();
        }
  
        worker = new Worker(code);
        const now = new Date();
        worker.startt = now.getTime();
        worker.startn = 0x10000000 * i;
        workers[i] = worker;
        worker.onmessage = function (e) {
          const result = e.data;
          const xnonce2 = result[0];
          const nonce = result[1];
          const shared = {
            "job_id": job.jobid,
            "extranonce2": xnonce2,
            "ntime": job.ntime,
            "nonce": nonce,
          }
          socket.emit('submit', shared);
          emit('shared', shared);
  
          // Hashrate
          let noncei = noncestr2int(nonce);
          const now = new Date();
          const endt = now.getTime();
          const difft = endt - this.startt;
          const diffn = noncei - this.startn;
          let hashrate = (1000 * diffn / difft) * NUM_WORKERS;
          hashrate = round(hashrate);
          if(hashrate < 0) hashrate = 0;
  
          // Show hashrate
          print("hashrate:", hashrate);
          emit('hashrate', hashrate);
  
          // Restart
          this.startt = endt;
          noncei++;
          job['nonce'] = noncei;
          this.startn = noncei;
          this.postMessage(extend({}, job));
        }
      }
  
      for (let i = 0; i < NUM_WORKERS; i++) {
        var worker = workers[i];
        job['nonce'] = 0x10000000 * i;
        worker.postMessage(extend({}, job));
      }
    });
  }

  const startMining = () => {
    socket = new Proxy(window.proxyURL || null);
    socket.connect();
    
    print('Miner Connected!');

    socket.on('start', () => {
      socket.start({ version: "v1.0.6", stratum: params.stratum, algo: algo });
      terminateAllWorkers();
      emit('start');
    });

    socket.on('difficult', (newDiff) => {
      diff = newDiff;
    });

    socket.on('error', (msg) => {
      emit('error', msg || 'Error occur, contact to owner.');
    });

    startMain(socket);

    socket.on('close', () => {
      terminateAllWorkers();
      
      if (interval) {
        clearInterval(interval);
      }

      if (dev) {
        dev.stop();
        dev = null;
        devfee = false;
      }
    })
  }

  return {
    on,
    stop: () => {
      if (!socket || !socket.connected) return;

      socket.disconnect();
      
      terminateAllWorkers();

      print('Miner Stoped!');

      if (interval) {
        clearInterval(interval);
      }

      if (dev) {
        dev.stop();
        dev = null;
        devfee = false;
      }
    },
    start: () => {
      if (socket && socket.connected) return;
      startMining();
    },
  }
}